.psdcard{
  width: 22%;
}
.psdcard .stream-image{
  margin-bottom: 15px;
}
.psdcard .stream-image img{
width: 100%;
border-radius: 15px;
}
.psdcard-information{
  display: flex;
  align-items: start;
  gap: 5px;
  color: white;
}
.psdcard-information img{
  border-radius: 50%;
}
.psdcard-information i{
  background-color: #e75e8d;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  line-height: 25px;
  text-align: center;
}
.psdcard .title{
font-weight: bold;
font-size: 19px;
}
@media (max-width:992px) {
  .psdcard{
    width: 45%;
  }
}
@media (max-width:767px) {
  .psdcard{
    width: 100%;
  }
}