.lstream{
  padding: 20px 0px;
  margin-bottom: 15px;
}
.lstream .lstream-info{
display: flex;
align-items: center;
justify-content: space-between;
color: white;
}
.lstream .lstream-info .per-image img{
  border-radius: 15px;
  margin-left: 5px;
}
.lstream .lstream-info .per-name{
  display: flex;
  align-items: center;
  gap: 5px;
}
.lstream .lstream-info .per-name i{
width: 30px;
height: 30px;
border-radius: 50%;
background-color: #ec6090;
display: flex;
align-items: center;
justify-content: center;
}
.lstream .btn{
float: right;
}
.lstream .btn button{
  background-color: #ec6090;
  border: none;
  border-radius: 15px;
  padding: 10px 20px;
  color: white;
}