.live-stream{
  position: relative;
}
.live-stream .live-container{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.live-stream .live-container>div{
  background-color: black;
  padding: 25px;
  border-radius: 25px;
}
.live-stream .live-container h1{
  color: white;
  margin-bottom: 15px;
}
.live-stream .live-container h1 span:first-child{
  text-decoration: underline;
  display: inline-block;
  margin-right: 5px;
}
.live-stream .live-container h1 span:last-child{
  color: #ec6090;
}
.live-stream .carousel .feat {
  height: 500px;
}
.live-stream   .carousel .feat-image{
  height: 400px;
  width: 100%;
  max-width: 100%;
} 
.live-stream .carousel .feat-image img{
width: 100%;
border-radius: 15px;
height: 100%;
}
.live-right{
  width: 60%;
}
.live-left{
  width: 34%;
}
@media (max-width:992px) {
  .live-stream .live-container{
    flex-direction: column;
    gap: 20px;
  }
  .live-stream .live-container>div{
    width: 100%;
  }
}
