.related-card{
  display: flex;
  justify-content: space-between;
  padding: 15px;
  align-items: center;
  color: white;
  padding: 30px 15px;
}
.related-card-right{
  display: flex;
  align-items: center;
  gap: 15px;
  font-size: 18px;
}
.related-card-right img{
  border-radius: 15px;
}
.related-card-left .rate {
  display: flex;
  gap: 5px;
  align-items: center;
  font-size: 16px;
}
.related-card-left .rate i{
color: rgb(255, 187, 0);
}
.related-card-left .download {
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 16px;
}
.related-card-left .download i {
  color: #ce537d;
}
