*{
  box-sizing: border-box;
  padding: 0%;
  margin: 0%;
}
.feature{
  background-color: #27292a;
  padding: 50px;
  border-radius: 25px;
}
.feature .feature-content{
  display: flex;
  justify-content: space-between;
  max-height: 100%;
}
.feature .feature-content >div{
  background-color: black;
  border-radius: 25px;
  padding: 25px;
}
.feature .feature-content .left{
  width: 50%;
}
.feature .feature-content .right{
  width: 45%;
}
.feature .feature-content  .head{
  margin-bottom: 30px;
}
.feature .feature-content .head h1 span{
  text-transform: capitalize;
  color: white;
  margin-bottom: 15px;
  font-size: 25px;
  margin-right: 5px;
  font-weight: bold;
}
.feature .feature-content .head h1 span:first-child{
  text-decoration: underline;
}
.feature .feature-content .head h1 span:last-child{
color: #ec4070;
}
.feature .feature-content .carousel .feat{
border-radius: 15px;
width: 100%;
text-align: center;
border-radius: 15px;
padding: 15px;
display: flex;
flex-direction: column;
height: 60%;
}
.feature .feature-content .left .carousel .feat {
  height: 500px;
}
.feature .feature-content .left .carousel .feat-image{
  height: 400px;
} 
 .feature .feature-content .left .carousel .feat-image img{
width: 100%;
border-radius: 15px;
height: 100%;
}
.feat-info{
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: center;
}
.feat-info .feat-top{
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  text-transform: capitalize;
}
.feat-info .feat-top i{
  color: rgb(255, 191, 0);
  display: inline-block;
  margin-right: 4px;
}
.feat-info .feat-bottom{
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-transform: capitalize;
  color: white;
}
.feat-info .feat-bottom i {
  color: #ec4070;
  display: inline-block;
  margin-right: 4px;
}

.feature .feature-content .right .right-lib{
padding: 15px;
}
.lib-link{
  text-align: center;
  margin-top: 10px;
  line-height: 100%;
}
.lib-link Link{
  text-decoration: none;
}
@media (max-width:992px) {
  .feature .feature-content{
    flex-direction: column;
  }
  .feature .feature-content >div{
    width: 100%;
  }
  .feature .feature-content .left{
    width: 100%;
  }
  .feature .feature-content .right{
    width: 100%;
  }
}