.lib-card{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px 0px 40px 0px;
  border-bottom: 1px solid #333;
}
.lib-card .card-left{
display: flex;
align-items: center;
gap: 15px;
}
.lib-card .card-left img{
  border-radius: 15px;
}
.lib-card .card-info{
color: white;
}
.lib-card .card-info h4{
  font-size: 18px;
  text-transform: capitalize;
}
.lib-card .card-info .nums{
display: flex;
justify-content: space-between;
gap: 15px;
color: white;
}
.lib-card .card-info .nums h6:first-child i{
  color: rgb(255, 153, 0) ;
  display: inline-block;
  margin-right: 5px;
}
.lib-card .card-info .nums h6:last-child i{
  color:#ec4070 ;
  display: inline-block;
  margin-right: 5px;
}
.lib-card .card-right{
  background-color: #333;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  color: #ec4070;
  font-size: 22px;
  cursor: pointer;
}