.cont {
  padding: 20px;
  width: 250px;
  border-radius: 20px;
  background-color: #27292a;
  margin: 0%;
}
.cont .image{
  width: 100%;
  margin-bottom: 15px;
}
.cont .image img{
  width: 100%;
  background-color: red;
  border-radius: 15px;
}
.cont .information {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.cont .information .text{
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 15px;
}
.cont .information .nums{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap:15px;
}
@media (max-width:767px) {
  .cont{
    padding: 10;
    margin: 0%;
    max-width: 100%;
  }
}