.mostpopular {
  padding: 50px 50px;
}
.mostpopular  h1{
  color: white;
}
.mostpopular .conta{
  background-color:black;
  padding:50px;
  border-radius:25px;
  position: relative;
}
.mostpopular  span:first-child{
  text-decoration: underline;
}
.mostpopular  span:last-child{
  color:#e75e8d ;
}
.mostpopular .row{
  display: flex;
  flex-wrap: wrap;
  row-gap: 20px;
}
.mostpopular .row .col{
  width: 300px;
  display: flex;
  justify-content: center;
  padding: 10px;
}
@media (max-width:992px) {
  .mostpopular .row .col{
    width: 250px;
  }
}
.mostpopular .conta .button{
  background-color: #e75e8d;
  color: white;
  text-transform: capitalize;
  width: 200px;
  height: 45px;
  border-radius: 35px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  text-decoration: none;
  line-height: 45px;
  font-size: 22px;
  bottom: -20px;
}
@media (max-width:767px) {
  .mostpopular{
    padding: 0%;
  }
  .mostpopular .row{
    width: 100%;
    padding: 0%;
    justify-content: center;
  }
  .mostpopular .conta{
    padding: 0%;
  }
}