.poplivestream{
  padding: 50px;
  position: relative;
}
.poplivestream-container{
  background-color: black;
  padding: 25px;
  border-radius: 25px;
}
.poplivestream-container >h1{
  margin-bottom: 20px;
}
.poplivestream-container >h1 span{
  color: white;
  text-transform: capitalize;
  font-weight: bold;
  font-size: 30px;
}
.poplivestream-container >h1 span:first-child{
text-decoration: underline;
display: inline-block;
margin-right: 5px;
}
.poplivestream-container >h1 span:last-child{
color: #e75e8d;
}
.poplivestream-content{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.poplivestream >button{
  color: white;
  background-color: #e75e8d;
  border: none;
  position: absolute;
  left: 50%;
  bottom: 25px;
  padding: 10px 20px;
  font-size: 20px;
  text-transform: capitalize;
  border-radius: 15px;
  transform: translateX(-50%);
}