.my-lib-prof{
  padding: 50px;
}
.my-lib-prof .my-lib-prof-container {
  padding: 35px;
  background-color: black;
  border-radius: 25px;
  position: relative;
}

.my-lib-prof .my-lib-prof-container .top-sec{
  border-bottom: 1px solid #d3d3d3;
  padding: 50px 0px 50px 0px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.my-lib-prof .my-lib-prof-container .top-sec >div{
  flex-basis: 30%;
}
.my-lib-prof .my-lib-prof-container .left-sec img{
  width: 100%;
  border-radius: 30px;
}
.my-lib-prof .my-lib-prof-container .center-sec button:first-child{
  background-color: #e75e8d;
  border: none;
  color: white;
  padding: 10px 20px;
  border-radius: 20px;
  margin-bottom: 10px;
}
.my-lib-prof .my-lib-prof-container .center-sec .name{
  color: white;
  text-transform: capitalize;
  margin-bottom: 15px;
  font-size: 20px;
  font-weight: bold;
}
.my-lib-prof .my-lib-prof-container .center-sec p{
  color: #27292a;
  margin-bottom: 20px;
}
.my-lib-prof .my-lib-prof-container .center-sec button:nth-of-type(2){
  color: #e75e8d;
  background-color: transparent;
  padding: 10px 20px;
  border: 1px solid #e75e8d;
  border-radius: 20px;
  font-size: 22px;
}
.my-lib-prof .my-lib-prof-container .right-sec .statistics{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
.my-lib-prof .my-lib-prof-container .right-sec .statistics .statistic{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.my-lib-prof .my-lib-prof-container .right-sec .statistics .statistic h4{
  color: #27292a;
}
.my-lib-prof .my-lib-prof-container .right-sec .statistics .statistic p{
  color: #e75e8d;
}
.clips{
  padding: 50px 0px;
}
.clips .head{
  color: white;
  margin-bottom: 40px;
  text-transform: capitalize;
  font-size: 35px;
}
.clips .head span:first-child{
text-decoration: underline;
display: inline-block;
margin-right: 5px;
}
.clips .head span:last-child{
color: #e75e8d;
}
.clips-content{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.clips-content .clip{
  width: 23%;
  padding: 15px;
  background-color: #27292a;
  border-radius: 25px;
}
.clips-content .clip img{
  width: 100%;
  border-radius: 25px;
  margin-bottom: 10px;
}
.clips-content .clip .clip-info{
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.clips-content .clip .clip-info .clip-name{
font-size: 15px;
font-weight: bold;
}
.clips-content .clip .clip-info .clip-stat{
  font-size: 15px;
  display: flex;
  align-items: center;
  gap: 5px;
}
.clips-content .clip .clip-info .clip-stat i{
  color: #e75e8d;
}
.my-lib-prof .my-lib-prof-container .button{
  color: white;
  text-decoration: none;
  width: 200px;
  position: absolute;
  height: 55px;
  border-radius: 40px;
  text-align: center;
  line-height: 55px;
  background-color: #e75e8d;
  left: 50%;
  transform: translateX(-50%);
}