.fortnite-detail{
  padding: 50px;
}
.fortnite-detail .fortnie-images{
  display: flex;
  gap: 15px;
  margin-bottom: 30px;
}
.fortnite-detail .fortnie-images .img-left{
  width: 30%;
  border-radius: 25px;
}
.fortnite-detail .fortnie-images .img-left img{
  width: 100%;
  border-radius: 25px;
}
.fortnite-detail .fortnie-images .img-right{
  width: 70%;
  border-radius: 25px;
}
.fortnite-detail .fortnie-images .img-right img{
  width: 100%;
  border-radius: 25px;
}
.fortnite-detail >h1{
  color: white;
  text-align: center;
  font-weight: bold;
}