nav .container{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0px;
}
nav .container form{
position: relative;
border: none;
height: 60px;
width: 250px;
border-radius: 60px;
}
nav .container form input{
  position: absolute;
  width: 100%;
  height: 100%;
  border: 1px solid #cdcdcd;
  border-radius: 30px;
  padding: 30px;
}
nav .container form i {
  position: absolute;
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
}
nav .container .links{
  list-style: none;
  display: flex;
  align-items: center;
  gap: 15px;
}
nav .container .links li a{
  text-decoration: none;
  color: white;
  font-size: 22px;
}