.related-game{
  padding: 50px;
}
.related-game-container{
  padding: 25px;
  border-radius: 25px;
  background-color: black;
}
.related-game-content{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.related-game-content >div{
  width: 50%;
}
.related-game-container >h1{
  color: white;
  margin-bottom: 25px;
  text-transform: capitalize;
}
.related-game-container >h1 span:first-child{
  text-decoration: underline;
}
.related-game-container >h1 span:last-child{
color: #e75e8d;
}
@media (max-width:992px) {
  .related-game-content{
    flex-direction: column;
  }
  .related-game-content >div{
    width: 100%;
  }
}