.about-fortnite{
  padding: 50px;
}
.about-fortnite .about-container{
  background-color: black;
  padding: 25px;
  border-radius: 25px;
}
.about-fortnite .about-container .about-top{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.about-fortnite .about-container .about-top >div{
  width: 48%;
  max-width: 100%;
  background-color: #27292a;
  padding: 25px;
  border-radius: 25px;
  height: 120px;
}
.about-fortnite .about-container .about-top .about-top-left .rate{
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  color: white;
}
.about-fortnite .about-container .about-top .about-top-left .rate i {
  color: rgba(255, 242, 0, 0.944);
}
.about-fortnite .about-container .about-top .about-top-left .download{
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  color: white;
}
.about-fortnite .about-container .about-top .about-top-left i{
  color:#e75e8d;
}
.about-fortnite .about-container .about-top .about-top-right{
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.about-fortnite .about-container .about-top .about-top-right h4{
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  gap: 5px;
}
.about-fortnite .about-container .about-top .about-top-right h4 i{
  color: #e75e8d;
}
.about-fortnite .about-container .about-top .about-top-right h4:first-child i{
  color: rgb(255, 166, 0);
} 
.about-fortnite .about-container .about-bottom .about-images{
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  flex-wrap: wrap;
}
.about-fortnite .about-container .about-bottom .about-images img{
  border-radius: 25px;
  width: 30%;
}
.about-fortnite .about-container .about-bottom p{
  color: #27a;
  margin-top: 20px;
}
.about-fortnite .about-container button{
  color:  #e75e8d;
  border: 1px solid #e75e8d;
  background-color: transparent;
  height: 60px;
  width: 100%;
  display: block;
  border-radius: 60px;
  font-size: 20px;
  text-transform: capitalize;
  margin-top: 10px;
}
@media (max-width:991px) {
  .about-fortnite .about-container .about-top >div{
    width: 100%;
    margin-bottom: 15px;
  }
}