.library{
  background-color: #27292a;
  padding: 20px;
}
.library .library-content{
  background-color: black;
  padding: 30px;
  border-radius: 25px;
  position: relative;
}
.library .library-content >h1{
  color: white;
  text-transform: capitalize;
  font-size: 25px;
  margin-bottom: 15px;
}
.library .library-content >h1 span:first-child{
  text-decoration: underline;
  margin-right: 7px;
}
.library .library-content >h1 span:last-child{
  color:#e75e8d;
}
.library .library-content .row
{
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  border-radius: 25px;
  border-radius: 25px;
  background-color: #27292a;
  overflow: hidden;
  padding: 10px 0px;
}
.library .library-content .row .col{
flex: 1;
width: 100%;
}
.library .library-content .button{
  background-color: #e75e8d;
  color: white;
  text-decoration: none;
  position: absolute;
  width: 200px;
  height: 55px;
  border-radius: 55px;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  line-height: 55px;
  font-size: 22px;
  bottom: -45px;
}