*{
  padding: 0%;
  margin: 0%;
  box-sizing: border-box;
}
.profile{
  padding: 50px;
}
.profile .container{
  padding: 50px;
  border-radius: 25px;
  max-width: 100%;
  position: relative;
  min-height: 60vh;
  background-image: url("../../images/banner-bg.jpg");
  background-size: cover;
  background-position: 45% ;
  background-repeat: no-repeat;
}
.profile .container .content{
position: absolute;
width: 60%;
max-width: 100%;
}
.profile .container .content h3{
  color: white;
  margin-bottom: 20px;
  font-size: 20px;
}
.profile .container .content h1{
  color: white;
  font-size: 70px;
  font-weight: bold;
}
.profile .container .content .button {
 border: none;
 color: white;
 background-color: #d63384;
 padding: 10px 20px; 
 border-radius: 20px;
 text-decoration: none;
}
@media (max-width:767px) {
  .profile{
    padding: 20px;
  }
  .profile .container{
    padding: 0%;
    width: 100%;
  }
  .profile .container .content{
    width: 100%;
    height: 100%;
    text-align: center;
  }
  .profile .container .content h1{
    font-size:15px;
    margin-bottom: 20px;
  }
}
@media (max-width:992px) {
  .profile .container .content{
    width: 100%;
    height: 100%;
  }
  .profile .container .content h1{
    font-size: 55px;
    margin-bottom: 20px;
  }
}