.pop-card{
  width: 22% ;
  border-radius: 15px;
}
.pop-card .image {
  border-radius: 15px;
  margin-bottom: 10px;
}
.pop-card .image img{
  width: 100%;
  border-radius: 15px;
}
.pop-card .pop-information{
  display: flex;
  align-items: start;
  gap: 20px;
}
.pop-card .pop-information img{
  border-radius: 50%;
}
.pop-card .pop-information .pop-info .streamer-name{
  display: flex;
  align-items: center;
  color: white;
  font-size: 18px;
  gap: 15px;
  align-items: center;
}
.pop-card .pop-information .pop-info .streamer-name i{
  background-color: #ec4070;
  border-radius: 50%;
  padding: 5px;
}
.pop-title{
  color: white;
  font-size: 20px;
}

@media (max-width:992px) {
  .pop-card {
    width: 40%;
  }
}