.game{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
/*   gap: 15px; */
justify-content: space-between;
  padding: 30px 10px;
  border-bottom: 2px solid #dcdcdc;
  width: 100%;
  background-color: #27292a;
}
.game h1{
  display: flex;
  flex-direction: column;
  color: white;
  font-size: 25px;
}
.game h1 span{
  color: white;
}
.game button{
  background-color: #e75e8d ;
  color: white;
  padding: 10px 20px ;
  border-radius: 15px;
  border: none;
}