.popular-stream{
  padding: 50px;
  position: relative;
}
.popular-stream .popular-stream-container{
  background-color: black;
  border-radius: 25px;
  padding: 30px;
}
.popular-stream .popular-stream-container >h1{
color: white;
margin-bottom: 25px;
font-weight: bold;
}
.popular-stream .popular-stream-container >h1 span:first-child{
  text-decoration: underline;
  display: inline-block;
  margin-right: 8px;
}
.popular-stream .popular-stream-container >h1 span:last-child{
  color: #ec4070;
}
.popular-stream .popular-stream-container .pop-content{
display: flex;
flex-wrap: wrap;
justify-content: space-between;
}
.popular-stream .pop-link{
  background-color: #ec4070;
  width: fit-content;
  margin: 15px auto 0px;
  padding: 15px 25px;
  border-radius: 25px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 20px;
}
@media (max-width:767px) {
  .popular-stream .popular-stream-container{
    padding: 20px;
  }
}