.stream-card{
  width: 300px;
  border: 2px solid #dedede;
  border-radius: 20px;
  color: white;
  padding: 20px;
}
.stream-card img{
  border-radius: 15px;
  margin-bottom: 15px;
}
.stream-card h2{
  margin-bottom: 10px;
}
@media (max-width:992px) {
  .stream-card{
    width: 100%;
  }
}