.live-stream{
  padding: 50px;
  width: 100%;
  position: relative;
}

.live-stream .live-head{
width: 500px;
max-width: 100%;
margin: 0px auto 50px;
color: white;
text-align: center;
width: 100%;
}
.live-stream .live-head h1 span:first-child{
  text-decoration: underline;
  display: inline-block;
  margin-right: 5px;
}
.live-stream .live-head span:last-child{
color: #e75e8d;
}
.live-stream .live-content{
  display: flex;
  gap: 15px;
  justify-content: center;
  max-width: 100%;
}
.live-stream .stream-link{
width: fit-content;
margin: 15px auto;
background-color: #e75e8d;
padding: 10px 20px;
border-radius: 15px;
}

@media (max-width:992px){
  .live-stream .live-content{
    flex-direction: column;
    align-items: center;
  }
}

.live-container .button{
  text-decoration: none;
  color: white;
  font-size: 22px;
  background-color: #e75e8d;
  width: 200px;
  height: 55px;
  display: flex;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 55px;
  position: absolute;
  left: 50%;
  bottom: -30px;
  cursor: pointer;
  transform: translateX(-50%);
}
